@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&family=Jacques+Francois+Shadow&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Qahiri&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@1,900&display=swap);
.particles-wrapper {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
  }
  
.header_nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: rgba(0, 0, 0, 0.849);
    color: #fff;
    z-index: 10;
    position: relative;
}
.nav_logo {
    flex: 0.4 1;
    display: flex;
    justify-content: center;
}
.nav_logo > h1 {
    font-family: 'Cinzel Decorative', cursive;
    letter-spacing: 1px;
    padding: 10px 0;
}
.nav_logo > img {
    text-align: center;
    padding: 10px 0;
}
.nav_logo:hover {
    box-shadow: rgb(255, 217, 0) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
}

.nav_logo span{
    font-family: 'Qahiri', sans-serif;
    color: #e3ff00;
}
.nav_menu {
    display: flex;
    flex: 0.6 1;
    justify-content: space-evenly;
    align-items: center;
}
.nav_menu a{
    text-decoration: none;
}
.nav_menu > a > h4 {
    font-size: 14px;
    font-family: "Roboto", sans-serif;
    color: #fff;
    text-decoration: none;
    transition: -webkit-transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out;
    transition: transform 50ms ease-in-out, -webkit-transform 50ms ease-in-out;
    
}

.nav_menu > a > h4:hover {
    color: #e3ff00;
    padding: 10px 0;
    border-radius: 5px;
    letter-spacing: 1px;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);

}
.main_banner {
    width: 100%;
    position: relative;
}

.hero_content {
    width: 100vw;
    max-height: 92vh;
    height: 92vh;
    background: linear-gradient(rgba(255, 255, 0, 0.233), #fff), url(/kiranlal_porto.git/static/media/main_banner.4baa3775.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    overflow-y: hidden;
}

.profile {
    display: flex;
    flex-direction: row-reverse;
    float: right;
    margin: 350px 100px 0 0;
    /* padding: 20px; */
    width: 40%;
    z-index: 3;
}
.bio {
    margin: auto;
    z-index: 13;
}

.bio_detail {
    text-align: left;
    padding-bottom: 5px;
}
.bio_detail h1 {
    font-family: 'Roboto', sans-serif;
    font-size: 50px;
}
.social_icons img {
    padding: 5px;
}
.social_icons a {
    transition: -webkit-transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out;
    transition: transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out;
}

.social_icons a:hover {
    padding: 5px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
}

.sub_tittle{
    padding: 50px 0;
    font-size: 50px;
}
section {
    overflow-y: hidden;
}
.who_am_i {
    display: grid;
    grid-template-rows: repeat(1, 1fr);
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px ;         
    position: relative;
    margin: auto;
    width: 80%;
    padding: 200px 100px;
}
.sub_title{
    padding: 50px 100px;
    font-size: 50px;
    text-align: right;
}

section {
    overflow-y: hidden;
}

.qualification {
    padding: 50px 100px;
}
.education_box {
    display: flex;
    flex-direction: row;
    margin: auto;
    justify-content: space-between;
}

.qualification .education {
    display: flex;
    flex-direction: column;
    text-align: right;
}

.qualification .education_1 {
    display: flex;
    flex-direction: column;
    padding: 20px 100px 20px 0;
}
.education_1 a {
    text-decoration: none;
    list-style: none;
    color: black;
}
.education_1 a, p {
    padding: 5px;
}
.sub_tittle{
    padding: 50px 0;
    font-size: 50px;
    text-align: center;
}
section {
    overflow-y: hidden;
}
.skills .sub_tittle {
    padding: 50px 100px 0px 50px;
}

.skill_content {
    display: flex;
    flex-direction: row;
    grid-gap: 30px ;         
    position: relative;
    margin: auto;
    width: 80%;
    padding: 50px 100px;
}

.skill_img{
    margin: auto;
}

.skill_icons {
    display: flex;
    flex-direction: column;
    grid-gap: 10px ;         
    position: relative;
    margin: auto;
}

.btn {
    cursor: pointer;
}

.btn:hover .skill{
    display: block;
}

.skilld{
    display: flex;
    flex-direction: column;
    padding: 0px 100px 50px 100px;
}

.expression {

    display: flex;
	flex-direction: row;
    /* justify-content: space-around; */
    margin: 20px auto;
    padding: 0 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    border-radius: 10px;
}

h3 {
	text-align: right;
}

.expression h3, .skil_card {
    padding: 10px 20px;
}
.work h2{
    padding: 50px 200px 50px 50px;
    font-size: 50px;
    text-align: right;
}

.work{
    display: flex;
    flex-direction: column;
    padding: 0px 100px 50px 100px;
}

.all_project{
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(3, 1fr);
    padding: 10px;
}

.some_projects{
    display: flex;
    flex-direction: column;
    margin: 20px 30px;
}

.some_projects p{
    text-align: center;
    padding: 10px 0px;
}

.project_0{
    text-align: center;
    padding: 30%;
    background: rgb(126, 126, 126);
}
.sub_tittle{
    padding: 50px 100px 50px 50px;
    font-size: 50px;
    text-align: center;
}

section {
    overflow-y: hidden;
}

.experience_content {
    display: grid;
    grid-template-rows: repeat(1, 1fr);
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px ;         
    position: relative;
    margin: auto;
    width: 80%;
    padding: 50px 100px;
}


.footer{
    margin: auto;
    width: 100%;
    padding: 50px 100px;
    background: #30303052;
}
.footer_logo {
    text-align: right;
    font-family: 'Cinzel Decorative', cursive;
    letter-spacing: 1 px;
    padding: 10px 20px 10px 0;
    color: #fff;
}

.footer_logo span{
    font-family: 'Qahiri', sans-serif;
    color: #e3ff00;
}

.footer_social img {
    padding: 5px;
}

.footer hr{
    width: 100%;
}
.footer .copyrights{
    padding: 20px 0 20px 20px;
}
* {
    margin: 0%;
    padding: 0%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    box-sizing: border-box;
}
