.sub_title{
    padding: 50px 100px;
    font-size: 50px;
    text-align: right;
}

section {
    overflow-y: hidden;
}

.qualification {
    padding: 50px 100px;
}
.education_box {
    display: flex;
    flex-direction: row;
    margin: auto;
    justify-content: space-between;
}

.qualification .education {
    display: flex;
    flex-direction: column;
    text-align: right;
}

.qualification .education_1 {
    display: flex;
    flex-direction: column;
    padding: 20px 100px 20px 0;
}
.education_1 a {
    text-decoration: none;
    list-style: none;
    color: black;
}
.education_1 a, p {
    padding: 5px;
}